@import url(https://fonts.googleapis.com/css?family=Rubik:300,regular,500,600,700,800,900,300italic,italic,500italic,600italic,700italic,800italic,900italic);
@import url(https://fonts.googleapis.com/css?family=Lato:100,100italic,300,300italic,regular,italic,700,700italic,900,900italic);
* {
    margin: 0;
    padding: 0;
  }
  
  input {
    height: 50px;
    background: #FFFFFF;
    border: 1px solid #555555;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 20px;
  }
  
  button {
    cursor: pointer;
  }
  
  button {
    font-family: 'Rubik', sans-serif;
  }

  .purple {
    color: #7B61FF
  }
  
  .orange {
    color: #FA8C10
  }
  
  .light-blue {
    color: #3A9FF1;
  }
  
  .light-red {
    color: #FF576B;
  }

  .bold{
    font-weight: bold;
  }
  
.modal-container {
    border-radius: 12px;
    position: absolute;
    background-color: #fefefe;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 500px;
    height: 700px;
  }

  .full-width {
    width: 100%;
  }
  
  .full-height {
    height: 100%;
  }

  .text-left {
    text-align: left;
  }

  .cursor-pointer {
    cursor: pointer;
  }
  
  .vertical-line {
    width: 1px;
    background-color: #EAEAEA;
  }
.container-header {
  width: 100%;
  height: 250px;
  background-image: linear-gradient(90.23deg, #05EDE3 0.02%, #645FFB 99.63%);
}

.container-header img {
  width: 170px;
  margin-left: 100px;
  margin-top: 40px;
}
.container-chip {
  width: 80px;
  height: 20px;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 5px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  transition: all 0.2s ease;
}

.container-chip:hover {
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.4);
}

.container-chip span {
  font-family: 'Rubik';
  font-size: 12px;
  margin-left: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.icon-filter {
  margin-right: 8px;
  width: 8px;
  height: 8px;
}

.selected {
  background-color: #7B61FF;
  color: #ffffff;
}
.open-filters-button {
  background: #FAFAFA;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border: none;
  padding: 10px 15px;
  display: flex;
  margin-bottom: 30px;
  transition: all 0.2 ease;
}

.open-filters-button:hover {
  opacity: 0.8;
}

.open-filters-button img {
  margin-right: 5px;
}

.custom-line {
  margin: 20px 0;
  height: auto;
}

.container-filters {
  height: 190px;
  background: #FAFAFA;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin: 15px 0 30px;
  display: flex;
  width: 950px;
}

.filter-item {
  display: flex;
  flex-direction: column;
  width: 220px;
  padding: 10px 20px;
}

.filter-item strong {
  font-family: 'Rubik';
  font-size: 13px;
  color: #B9B9B9;
}

.container-chips {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 80%;
  margin-top: 18px;
}

.container-input-filter label {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  color: #484848;
}

.container-input-filter input {
  background: #FFFFFF;
  border: 1px solid #7B61FF;
  box-sizing: border-box;
  border-radius: 2px;
  width: 100px;
  height: 20px;
  padding: 15px 5px;
}

.container-input-filter:first-of-type {
  margin-top: 18px;
}

.container-input-filter:last-of-type {
  margin-top: 12px;
}

.container-actions {
  display: flex;
  align-items: flex-end;
}

.container-actions button {
  border: none;
  width: 120px;
  height: 40px;
  border-radius: 10px;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.1);
  font-family: 'Rubik';
  font-weight: 600;
  margin: 0 18px 18px 0;
  transition: all 0.2 ease;
}
.container-actions button:hover {
  opacity: 0.8;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.4);
}

.btn-apply-filters {
  background: linear-gradient(264.83deg, #3A9FF1 -3.28%, #05EDE3 100%);
  color: #ffffff;
}

.btn-clear-filters {
  background: #FAFAFA;
}
.container-confirm-delete {
  position: absolute;
  top: 45px;
  left: 80px;
  width: 120px;
  height: 50px;
  padding: 7px;
  margin: 5px;
  background: #E4F2FD;
  border-radius: 4px;
  z-index: 1;
}

.container-confirm-delete span {
  display: block;
  font-size: 10px;
  font-family: 'Rubik';
  font-weight: 300;
  margin: 8px;
}

.btn-actions-confirm-delete {
  margin: 0px 3px 0px 3px;
  padding: 5px;
  width: 40px;
  border: none;
  border-radius: 7px;
  color: #ffffff;
  font-size: 9px;
  font-weight: 500;
}

.btn-actions-confirm-delete:hover {
  opacity: 0.8;
}

.blue {
  background-color: #3A9FF1;
}

.red {
  background-color: #FF576B;
}

.arrow-up {
  position: absolute;
  top: -10px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #E4F2FD;
}
.table-head {
  height: 45px;
  background: #FAFAFA;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.column-title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 45px;
  width: 150px;
}

.column-title span {
  margin-right: 5px;
}

.order-column {
  width: 10px;
  margin-top: 5px;
}
.table {
  width: 950px;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  line-height: 17px;
}

.table-body {
  display: flex;
  flex-direction: column;
}

.table-line {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #E0E0E0;
}

.line-items {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 150px;
  height: 55px;
}

.edit-icon {
  margin-right: 20px;
  width: 18px;
  cursor: pointer;
}

.delete-icon {
  width: 18px;
  cursor: pointer;
}

.action-buttons {
  position: relative;
}
.container-resume {
  box-sizing: border-box;
  width: 230px;
  height: 200px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  background: #FAFAFA;
  padding: 26px 33px;
  border-radius: 10px;
  font-family: 'Rubik', Verdana, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  color: #2F2F2F;
}
.container-resume div {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  margin-top: 20px;
}

.container-resume div:nth-last-of-type(1) {
  border-top: 1px solid #EAEAEA;
  margin-top: 15px;
  padding-top: 15px;
  font-size: 14px;
}

.in {
  color: #645FFB
}

.out {
  color: #FA8C10
}

.balance {
  color: #3A9FF1;
}

.btn-add {
  width: 100%;
  height: 42px;
  margin-top: 40px;
  border: none;
  background: linear-gradient(91.26deg, #FA8C10 0%, #FF57D3 97.77%);
  border-radius: 5px;
  color: #ffffff;
  font-weight: bold;
  font-size: 12px;
  transition: all 0.2s ease;
}

.btn-add:hover {
  opacity: 0.8;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.4);
}
.backdrop {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(61, 61, 61, 0.48);
}

.close-icon {
  width: 15px;
  position: absolute;
  right: 55px;
  top: 55px;
  cursor: pointer;
}

.modal-container {
  border-radius: 20px;
  position: absolute;
  background-color: #fefefe;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 500px;
  height: 700px;
}

.modal-container input {
  width: 100%;
  margin: 5px 0 20px 0;
}

.modal-container form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
  height: 60%;
}

.modal-container h2 {
  font-family: 'Rubik', sans-serif;
  font-size: 30px;
  text-align: left;
  width: 90%;
}

.modal-container label {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  text-align: left;
  width: 90%;
}

.container-buttons {
  width: 90%;
}

.container-btn-insert {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.btn-insert {
  width: 40%;
  height: 35px;
  border: none;
  background: #645FFB;
  border-radius: 5px;
  font-family: 'Rubik', sans-serif;
  font-size: 14px;
  color: #fff;
  transition: all 0.2s ease;
}

.btn-insert:hover {
  opacity: 0.8;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.4);
}

.btn-new-transaction {
  width: 50%;
  height: 50px;
  border: none;
  padding: 5px;
  background-color: #B9B9B9;
  font-size: 14px;
  font-weight: bold;
  color: #ffffff;
  transition: all 0.2s ease;
}

.btn-new-transaction:hover {
  opacity: 0.9;
  box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.2);
}

.btn-new-transaction:first-child {
  border-radius: 10px 0px 0px 10px;
}

.btn-new-transaction:last-child {
  border-radius: 0px 10px 10px 0px;
}

.credit {
  background: linear-gradient(91.26deg, #05EDE3 0%, #3A9FF1 97.77%);
}

.debit {
  background: linear-gradient(91.66deg, #FA8C10 0%, #FF576B 90.32%);
}
.container-dashboard {
  display: flex;
  flex-direction: column;
}

main {
  display: flex;
  justify-content: center;
  background-color: #ffffff;
  position: relative;
  border-radius: 60px 60px 0px 0px;
  top: -120px;
  padding-top: 55px;
  grid-gap: 40px;
  gap: 40px;
}

.aside-right {
  margin-top: 70px;
}
